module.exports = "/*--------------------\nForm\n---------------------*/\nlabel {\n  margin-bottom: 2px;\n}\nform {\n  margin-top: 20px;\n}\nform p {\n  margin-bottom: 10px;\n}\ninput[type=text],\ninput[type=email],\ninput[type=password] {\n  width: 300px;\n  height: 32px;\n  padding: 0 10px;\n  margin-bottom: 10px;\n  background-color: rgba(0, 0, 0, 0.03);\n  border: none;\n  display: inline;\n  color: #303030;\n  font-size: 16px;\n  font-weight: 400;\n}\n::-webkit-input-placeholder {\n  /* Chrome, Firefox, Opera, Safari 10.1+ */\n  opacity: 0.7;\n  /* Firefox */\n}\n::-moz-placeholder {\n  /* Chrome, Firefox, Opera, Safari 10.1+ */\n  opacity: 0.7;\n  /* Firefox */\n}\n:-ms-input-placeholder {\n  /* Chrome, Firefox, Opera, Safari 10.1+ */\n  opacity: 0.7;\n  /* Firefox */\n}\n::-ms-input-placeholder {\n  /* Chrome, Firefox, Opera, Safari 10.1+ */\n  opacity: 0.7;\n  /* Firefox */\n}\n::placeholder {\n  /* Chrome, Firefox, Opera, Safari 10.1+ */\n  opacity: 0.7;\n  /* Firefox */\n}\ninput[type=text]:focus,\ninput[type=email]:focus,\ninput[type=password]:focus {\n  background-color: rgba(0, 0, 0, 0.05);\n  /*background-color: rgba(2,143,118,0.15);*/\n  outline: none;\n  box-shadow: none;\n}\n.checkbox-field label {\n  display: inline;\n}\n.ng-valid[required], .ng-valid.required {\n  border-left: 5px solid #42A948;\n  /* green */\n}\n.ng-invalid:not(form) {\n  border-left: 5px solid #a94442;\n  /* red */\n}\n/*# sourceMappingURL=data:application/json;base64, */"