module.exports = "/* ------------------------------------------------------------ */\n/* overlay style */\n/* ------------------------------------------------------------ */\n.nfc-token {\n  opacity: 0;\n  position: absolute;\n}\n.nfc-overlay p {\n  margin: 0;\n  margin-bottom: 6px;\n  color: #028F76;\n}\n.nfc-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  right: 0;\n  overflow-x: hidden;\n  overflow-y: auto;\n  z-index: 10012;\n}\n.nfc-overlay-background {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.5);\n  right: 0;\n  z-index: 10010;\n}\n/* NFC Overlay */\n.nfc-overlay-content {\n  width: 70%;\n  position: relative;\n  margin: 150px auto;\n  padding: 30px 30px 30px;\n  background-color: #fdfdfd;\n  border: 7px solid #ccc;\n  border-radius: 8px;\n  z-index: 10013;\n  /* 1px higher than the overlay layer */\n}\n@media all and (min-width: 800px) {\n  .nfc-overlay-content {\n    width: 650px;\n  }\n}\n@media all and (max-height: 800px) {\n  .nfc-overlay-content {\n    margin: 50px auto;\n  }\n}\n.nfc-overlay-content .close {\n  margin: -20px -14px;\n  float: right;\n  text-align: right;\n  font-size: 18px;\n  font-weight: bold;\n  line-height: 1;\n  opacity: 0.3;\n  text-decoration: none;\n}\n.nfc-overlay-content .close:hover {\n  cursor: pointer;\n  opacity: 0.5;\n}\n.nfc-overlay-content h2 {\n  margin-top: 6px;\n  margin-bottom: 14px;\n  font-weight: bold;\n  letter-spacing: 0.5px;\n}\n.nfc-overlay-content img {\n  max-width: 100%;\n  height: auto;\n  vertical-align: middle;\n  border: 0;\n}\n/*# sourceMappingURL=data:application/json;base64, */"