module.exports = "/* ------------------------------------------------------------ */\n/* Checkbox toggle */\n/* source: https://www.w3schools.com/howto/howto_css_switch.asp */\n/* ------------------------------------------------------------ */\n/* The switch - the box around the slider */\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 34px;\n  height: 19px;\n  margin-bottom: -4px;\n  margin-left: 2px;\n  margin-right: 10px;\n}\n/* Hide default HTML checkbox */\n.switch input {\n  display: none;\n}\n/* The slider */\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #ccc;\n  -webkit-transition: 0.4s;\n  transition: 0.4s;\n}\n.slider:before {\n  position: absolute;\n  content: \"\";\n  height: 13px;\n  width: 13px;\n  left: 3px;\n  bottom: 3px;\n  background-color: white;\n  -webkit-transition: 0.2s;\n  transition: 0.2s;\n}\ninput:checked + .slider {\n  background-color: #AEDE1A;\n}\ninput:focus + .slider {\n  box-shadow: 0 0 1px #AEDE1A;\n}\ninput:checked + .slider:before {\n  -webkit-transform: translateX(15px);\n  transform: translateX(15px);\n}\n/* for Angular methodes */\n.active .slider {\n  background-color: #AEDE1A;\n}\n.active .slider:before {\n  -webkit-transform: translateX(15px);\n  transform: translateX(15px);\n}\n/* Rounded sliders */\n.slider.round {\n  border-radius: 19px;\n}\n.slider.round:before {\n  border-radius: 50%;\n}\n/*# sourceMappingURL=data:application/json;base64, */"